.teampage {
    position: relative;

    .lines {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        z-index: 1;
        pointer-events: none;

        &__wrap {
            max-width: 1200px;
            height: 100%;
            margin: 0 auto;
            position: relative;
        }
    
        &__item {
            width: 1px;
            height: 100%;
            background-color: rgba(234, 234, 234, 0.102);
            //background-color: @white;
            position: absolute;
            right: 0;
            top: 0;

            &:nth-of-type(4) {
                right: 200px;
            }

            &:nth-of-type(3) {
                right: 400px;
            }

            &:nth-of-type(2) {
                right: 600px;
            }

            &:nth-of-type(1) {
                right: 800px;
            }
        }
    }
}

.team-list {
    &__item {
        &:nth-of-type(even) {
            background-color: @darkerBlue;
        }

        &:nth-of-type(odd) {
            background-color: @darkBlue;
            
            .team-list__wrap {
                position: relative;

                &:before {
                    content: '';
                    position: absolute;
                    right: 176px;
                    bottom: -9px;
                    width: 4px;
                    height: 46px;
                    background-color: @violet;
                } 
            }   
        }
    }

    &__wrap {
        display: flex;
        align-items: center;
        padding: 60px 0 60px 70px;
    }

    &__img {
        min-width: 220px;
        width: 220px;
        height: 220px;
        border-radius: 300px;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__content {
        padding-left: 140px;
    }

    &__name {
        color: @violet;
        font-weight: 700;
        font-size: 30px;
        position: relative;

        &:before {
            content: '';
            position: absolute;
            top: 10px;
            left: -61px;
            width: 25px;
            height: 27px;
            background-image: url("../img/icons/shield.png");
            background-size: cover;
        }
    }

    &__label {
        font-size: 17px;
        font-weight: 700;
        margin: 7px 0 27px;
    }

    &__text {
        margin-bottom: 19px;

        p {
            font-size: 15px;
            font-weight: 400;
            line-height: 1.35;
        }
    }

    &__more {
        font-size: 13px;
        font-weight: 700;
        position: relative;
        color: @green;

        &:after {
            content: "";
            position: absolute;
            right: -28px;
            top: 0;
            bottom: 0;
            margin: auto;
            width: 16px;
            height: 17px;
            background-image: url("../img/icons/arrow-green.png");
            background-size: cover;
        }
    }
}